.container {
  width: 630px;
  box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.43);
  margin: 80px auto;
}

@media (max-width: 768px) {
  .container {
    width: 330px;
  }
}

@media (min-width: 0) and (max-width: 449px) {
  .container {
    width: 100%;
  }
}

.grid {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  box-sizing: border-box;
  margin: -15px -15px -15px -15px;
  width: calc(100% + 30px);
  overflow: hidden;
}

.grid > * {
  position: relative;
  box-sizing: content-box;
  flex-grow: 0;
  flex-shrink: 0;
  -ms-flex-order: 999999;
  order: 999999;
}

.grid > *:nth-child(1) {
  -ms-flex-order: 10;
  order: 10;
}

.grid > *:nth-child(2) {
  -ms-flex-order: 20;
  order: 20;
}

.grid > *:nth-child(3) {
  -ms-flex-order: 30;
  order: 30;
}

.grid > *:nth-child(4) {
  -ms-flex-order: 40;
  order: 40;
}

.grid__item {
  flex-basis: calc(100% - 30px);
  padding: 0 15px 0 15px;
  overflow: inherit;
}

.grid__item--defaultHeight {
  height: 300px;
}

@media (max-width: 768px) {
  .grid__item--defaultHeight {
    height: auto;
  }
}

.grid__item--halfLeft, .grid__item--halfRight {
  flex-basis: calc(50% - 30px);
  padding: 0 15px 0 15px;
  overflow: inherit;
  flex-basis: calc(50% - 15px);
}

@media (max-width: 768px) {
  .grid__item--halfLeft, .grid__item--halfRight {
    flex-basis: calc(100% - 30px);
    padding: 0 15px 0 15px;
    overflow: inherit;
  }
}

@media (min-width: 768px) {
  .grid__item--halfLeft {
    padding-right: 0;
  }
}

@media (min-width: 768px) {
  .grid__item--halfRight {
    padding-left: 0;
  }
}

body {
  font-family: 'Catamaran', 'Helvetica', 'Arial', sans-serif;
  color: #A8A8A8;
}

a {
  color: #fff;
  text-decoration: none;
}

a:hover {
  color: #A8A8A8;
}

.portrait {
  position: relative;
  height: 100%;
  min-height: 300px;
  background-position: top right;
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-image: url("../images/photo-martin-neundorfer-richtig-so-20160801.jpg");
}

.portrait__logo {
  position: relative;
  display: inline-block;
  width: 85px;
  height: 130px;
  background-image: url("../images/martin-neundorfer-logo-desktop-20160801.png");
  background-size: 85px 130px;
  background-position: left top;
  background-repeat: no-repeat;
  margin-top: 30px;
  margin-left: 30px;
}

@media (max-width: 768px) {
  .portrait__logo {
    display: none;
  }
}

.mobileLogo {
  display: none;
}

@media (max-width: 768px) {
  .mobileLogo {
    display: block;
    background-color: #fff;
    margin-top: 40px;
  }
}

.mobileLogo__logo {
  display: block;
  width: 180px;
  height: 50px;
  background-image: url("../images/martin-neundorfer-logo-mobile-20160801.png");
  background-size: 180px 50px;
  background-position: left top;
  background-repeat: no-repeat;
  margin: 30px;
}

.box {
  height: 100%;
  padding: 30px;
  color: #fff;
  font-size: 18px;
}

.box--colorA {
  background-color: #D6BA6A;
}

.box--colorB {
  background-color: #71C3BB;
}

.box--colorC {
  background-color: #9FCC8C;
}

.box--colorD {
  background-color: #DE815D;
}

.box p {
  color: #fff;
  line-height: 25px;
  margin-bottom: 10px;
  width: 100%;
}

.box p.narrow {
  max-width: 400px;
}

.box p a {
  text-decoration: underline;
}

.box h3 {
  color: #fff;
  padding: 0;
  margin: 0;
  margin-bottom: 15px;
  font-size: 25px;
  text-transform: uppercase;
  font-weight: 700;
}

.box .subheadline {
  color: #fff;
  font-weight: 700;
}

.socialLinks {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 15px;
  display: inline-flex;
}

.socialLinks__item {
  font-size: 35px;
  margin-right: 30px;
}

.cool {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-image: url("../images/cool.svg");
  vertical-align: -3px;
}

