@media print {
  * {
    color: #000 !important;
  }
  .container {
    width: 100%;
    box-shadow: none;
  }
  .noPrint {
    display: none;
  }
  .print__logo {
    margin-bottom: 20px;
  }
  .print__portrait {
    width: 100%;
  }
  .grid__item--defaultHeight {
    min-height: auto !important;
  }
  h3 {
    page-break-before: always;
    page-break-after: avoid;
  }
  p {
    page-break-before: avoid;
  }
}

@media screen {
  .print {
    display: none;
  }
}
